import Vue from 'vue'
import Layout from './components/layout.vue'
import './registerServiceWorker'
import router from './router'
import VueResource from 'vue-resource'
import store from './store'
Vue.use(VueResource) 

Vue.config.productionTip = false

new Vue({
  router,
  store,
  render: h => h(Layout)
}).$mount('#app')
