import Vue from 'vue'
import VueRouter from 'vue-router'

Vue.use(VueRouter)

const routes = [
  {
    path:'/',
    redirect: '/index'
  },
  {
    path:'/index',
    component:()=>import('views/index')
  },
  {
    path:'/orderList',
    component:()=>import('views/orderList')
  },
  {
    path:'/detail',
    redirect:'/detail/count',
    component:()=>import('views/detail'),
    children:[
      {
        path:'count',
        component:()=>import('views/detail/count')
      },
      {
        path:'analysis',
        component:()=>import('views/detail/analysis')
      },
      {
        path:'publish',
        component:()=>import('views/detail/publish')
      },
      {
        path:'forecast',
        component:()=>import('views/detail/forecast')
      }
    ]
  }
]

const router = new VueRouter({
  // mode: 'history',
  base: process.env.BASE_URL,
  routes,
  linkActiveClass:'active'
})

export default router
