<template>
  <div class="reg-form">
     <div class="r-form">
      <span>用户名:</span>
      <div class="r-form-input">
       <input type="text" placeholder="请输入用户名" v-model="username">
       <i>
        {{userErrors.text}}
       </i>
       </div>
    </div>
    <div class="r-form">
      <span>密码 &nbsp;&nbsp;:</span>
      <div class="r-form-input">
       <input type="password" placeholder="请输入密码 6-18位" v-model="passname">
       <i>
        {{passErrors.text}}
       </i>
       </div>
    </div>
    <div class="r-form">
      <span>手机号:</span>
      <div class="r-form-input">
       <input type="text" placeholder="请输入手机号" maxlength="11" v-model="telname">
       <i>
        {{telErrors.text}}
       </i>
       </div>
    </div>
    <div class="button-a">
      <a @click="onRegClick">立即注册</a>
    </div>
    <p class="text-p">
      {{text}}
    </p>
  </div>
</template>

<script>
export default{
    name:'RegForm',
    data () {
      return {
        username:undefined,
        passname:undefined,
        telname:undefined,  
        text:''
      }
    },
    methods: {
      onRegClick(){
        if(!this.userErrors.status || !this.passErrors.status || !this.telErrors.status){
           this.text= '部分选项没有通过'
        }else{
          this.$http.get('http://localhost:3000/login').then
        (value=>{
          this.$emit('has-res-click',value.body)
        },res=>{

        })
        }
      }
    },
    computed: {
      userErrors(){
        let text,status
        if(!/@/g.test(this.username)){
          text = '用户名不包含@'
          status = false
        }else{
          text = ''
          status = true
          this.text = ''
        }
        if(!this.fale){
          text = ''
          this.fale = true
        }
        return {
          text,
          status
        }
      },
      passErrors(){
         let text,status
        if(!/^[a-zA-Z]\w{5,17}$/.test(this.passname)){
          text = '密码必须是字符开头，只能包含字符、数字和下划线 6-18位'
          status = false
        }else{
          text = ''
          status = true
           this.text = ''
        }
        if(!this.fale){
          text = ''
          this.fale = true
        }
        return {
          text,
          status
        }
      },
       telErrors(){
         let text,status
        if(!/\d/g.test(this.telname)){
          text = '手机号必须是数字'
          status = false
        }else{
          text = ''
          status = true
           this.text = ''
        }
        if(!this.fff){
          text = ''
          this.fff = true
        }
        return {
          text,
          status
        }
      }
    }
}
</script>

