import Vue from 'vue'

const state={
    orderList:[],
    params:{} 
}

const getters={
    getOrderList:state=> state.orderList
}

const actions={
    fetchOrderList({ commit, state}){
        Vue.http.get('http://localhost:3000/getOrderList',state.params).then(
            res=>{
                console.log(res);
                commit('changePrderList',res.body.list)
            }
        )
    }
}

const mutations={
    changePrderList(state,payload){
        state.orderList = payload
    },
    updateParams(state,{key,val}){
        state.params[key] = val
        console.log(state.params);
    }
}
export default{
    state,
    getters,
    actions,
    mutations
}