<template>
  <div @click="resetSelect">
    <div class="app-head">
        <div class="app-head-inner">
          <router-link :to="{path:'/'}">
            <img src="~assets/logo.png">
          </router-link>
            <div class="head-nav">
              <ul class="nav-list">
                <li class="hoverActive">
                  {{name}}
                </li>
                <li v-if="name !== ''" class="nav-pice">|</li>
                <li v-if="name !== ''" @click="login" class="hoverActive">退出</li>
                <li v-if="name === ''" @click="logClick" class="hoverActive">登录</li>
                <li v-if="name === ''" class="nav-pice">|</li>
                <li v-if="name === ''" @click="regClick" class="hoverActive">注册</li>
                <li class="nav-pice">|</li>
                <li @click="aboutClick" class="hoverActive">关于</li>
              </ul>
            </div>
        </div>
    </div>
    <div class="app-content">
     <keep-alive>
       <router-view/>
     </keep-alive>
    </div>
    <div class="app-foot">
      <p>© 2016 fishenal MIT</p>
    </div>
    <di-alog :is-show="isShowAboutDialog" @on-close="closeDialog('isShowAboutDialog')">
      <p>
        用户行为指标
        用户行为指标主要反映用户是如何来到网站的、在网站上停留了多长时间、访问了哪些页面等，主要的统计指标包括：
        用户在网站的停留时间；
        用户来源网站（也叫“引导网站”）；
        用户所使用的搜索引擎及其关键词；
        在不同时段的用户访问量情况等。
        浏览网站方式
        用户上网设备类型
        用户浏览器的名称和版本
        访问者电脑分辨率显示模式
        用户所使用的操作系统名称和版本
        用户所在地理区域分布状况等
      </p>
    </di-alog>
      <di-alog :is-show="isShowLogDialog" @on-close="closeDialog('isShowLogDialog')">
      <log-form @has-log="onSuccessLog"></log-form>
    </di-alog>
      <di-alog :is-show="isShowRegDialog" @on-close="closeDialog('isShowRegDialog')">
      <reg-form @has-res-click="onSuccessReg"></reg-form>
    </di-alog>
  </div>
</template>

<script>
 import DiAlog from 'components/base/dialog'
 import LogForm from './logForm'
 import RegForm from './regForm'
 import {eventBus} from '../eventBus'
export default {
  components: {
   DiAlog,
   LogForm,
   RegForm
  },
  data () {
    return {
      isShowAboutDialog:false,
      isShowLogDialog:false,
      isShowRegDialog:false,
      name:''
    }
  },
  methods: {
    aboutClick(){
      this.isShowAboutDialog = true
    },
    logClick(){
       this.isShowLogDialog = true
    },
    regClick(){
       this.isShowRegDialog = true
    },
     closeDialog(arr){
       this[arr] = false
    },
    onSuccessLog(val){
      this.name=val.username
      this.closeDialog('isShowLogDialog')
    },
    login(){
      this.name=''
    },
    onSuccessReg(con){
       this.name=con
       this.closeDialog('isShowRegDialog')
    },
    resetSelect(){
      eventBus.$emit('reset-select')
    }
  }
}
</script>

<style>

html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed, 
figure, figcaption, footer, header, hgroup, 
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}
/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure, 
footer, header, hgroup, menu, nav, section {
  display: block;
}
body {
  line-height: 1;
}
ol, ul {
  list-style: none;
}
blockquote, q {
  quotes: none;
}
blockquote:before, blockquote:after,
q:before, q:after {
  content: '';
  content: none;
}
table {
  border-collapse: collapse;
  border-spacing: 0;
}
a {
  color: inherit;
  text-decoration: none;
}
body {
  background: #f0f2f5;
  font-family: "Helvetica Neue",Helvetica,Arial,"Hiragino Sans GB","Hiragino Sans GB W3","Microsoft YaHei UI","Microsoft YaHei","WenQuanYi Micro Hei",sans-serif;
  font-size: 14px;
  color: #444;
}
.app-head {
  background: #363636;
  color: #b2b2b2;
  height: 90px;
  line-height: 90px;
  width: 100%;
}
.app-head-inner {
  width: 1200px;
  margin: 0 auto;
  height: 90px;
}
.head-logo {
  float: left;
}
.app-head-inner img {
  width: 50px;
  margin-top: 20px;
}
.head-nav {
  float: right;
}
.head-nav ul {
  overflow: hidden;
}
.head-nav li {
  cursor: pointer;
  float: left;
}
.nav-pile {
  padding: 0 10px;
}
.app-foot {
  text-align: center;
  height: 80px;
  width: 100%;
  line-height: 80px;
  background: #e3e4e8;
  clear: both;
  margin-top: 30px;
}
.container {
  width: 1200px;
  margin: 0 auto;
}
.hr {
  height: 1px;
  width: 100%;
  background: #ddd;
}
.button {
  background: #4fc08d;
  color: #fff;
  display: inline-block;
  padding: 10px 20px;
  cursor: pointer;
}
.button:hover {
  background: #4fc08d;
}
.g-form-line {
  padding: 15px 0;
}
.g-form-label {
  width: 100px;
  font-size: 16px;
  display: inline-block;
}
.g-form-input {
  display: inline-block;
}
.g-form-input input {
  height: 30px;
  width: 200px;
  line-height: 30px;
  vertical-align: middle;
  padding: 0 10px;
  border: 1px solid #ccc;
}
.g-form-btn {
  padding-left: 100px;
}
.g-form-error {
  color: red;
  padding-left: 15px;
}
.nav-pice{
  margin: 0 5px;
}
.err-p{
  color: red;
}
.hoverActive:hover{
  color: #fff;
  transform: translate(1px,2px);
}
</style>