<template>
  <div class="login-form">
    <div class="g-form">
        <span class="g-form-label">用户名:</span>
        <div class="g-form-input">
            <input type="text"
            v-model="usernameModel" placeholder="请输入用户名">
        </div>
        <span class="g-form-error">
            {{userErrors.errorText}}
        </span>
    </div>
    <div class="g-form-line">
        <span class="g-form-label">密码:</span>
        <div class="g-form-input">
            <input type="password"
            v-model="passwordModel" placeholder="请输入密码 1~6位">
        </div>
         <span class="g-form-error">
            {{passwordErrors.errorText}}
        </span>
    </div>
    <div class="g-form-line">
        <div class="g-form-btn">
            <a class="button" @click="onLogin">登录</a>
        </div>
    </div>
    <p class="err-p">
        {{errorText}}
    </p>
  </div>
</template>

<script>
export default{
    name:'LogForm',
    data () {
        return {
            usernameModel:'',
            passwordModel:'',
            errorText:''
        }
    },
    computed: {
        userErrors(){
            let errorText, status
            if(!/@/g.test(this.usernameModel)){
                status=false
                errorText = '不包含@'
            }else{
                status=true
                errorText = ''
                this.errorText=''
            }
            if(!this.userFlag){
                errorText = '',
                this.userFlag = true
            }
            return{
                errorText,
                status
            }
        },
         passwordErrors(){
            let errorText, status
            if(!/^\w{1,6}$/g.test(this.passwordModel)){
                status=false
                errorText = '密码不超过1到6位'
            }else{
                status=true
                errorText = ''
                this.errorText=''
            }
            if(!this.passwordFlag){
                errorText = '',
                this.passwordFlag = true
            }
            return{
                errorText,
                status
            }
        }
    },
    methods: {
        onLogin(){
            if(!this.userErrors.status || !this.passwordErrors.status){
                 this.errorText='部分选项卡未通过'
            }else{
               this.$http.get('http://localhost:3000/login')
               .then(value=>{
               this.$emit('has-log',value.body)
               },err=>{
                // console.log(err);
               })
            }
        }
    }
}
</script>

